import React from "react";
import PropTypes from "prop-types";
import ExpandCollapse from "react-expand-collapse";

function Post({ post, isExpand }) {
  const { owner, title, content, tags } = post;
  const Content = isExpand ? (
    <ExpandCollapse previewHeight="100px">{content}</ExpandCollapse>
  ) : (
    <p>{content}</p>
  );

  return (
    <div>
      <h5>{owner}</h5>
      <h4>{title}</h4>
      <h6>
        {tags.map(tag => (
          <span>{`#${tag}`}</span>
        ))}
      </h6>
      {Content}
    </div>
  );
}

Post.propTypes = {
  post: PropTypes.objectOf(PropTypes.string).isRequired,
  isExpand: PropTypes.bool,
};

Post.defaultProps = {
  isExpand: false,
};

export default Post;
